* {
  margin: 0;
  padding: 0;
  font-family: 'JetBrains Mono';
}

.app-container {
  background: linear-gradient(to right bottom, #1d1d28, #212125);
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.todo-container {
  background-color: #161617;
  min-width: 450px;
  min-height: 650px;
  max-height: 650px;
  padding: 30px;
  box-sizing: border-box;
  border-radius: 10px;
  box-shadow: 3px 6px 30px #a427a4;
  margin-bottom: 10px;
  overflow-y: auto;
}

.todo-container::-webkit-scrollbar {
  background-color: inherit;
  width: 12px;
}

.todo-container::-webkit-scrollbar-thumb {
  background-color: #333232;
  border-radius: 10px;
}

.header h1 {
  color: #e6ecf0;
  text-align: center;
  margin: 30px 0;
}

.todo-input {
  outline: none;
  width: 260px;
  padding: 15px;
  margin-right: 25px;
  font-size: 20px;
  color: #ccc;
  background-color: #333232;
  border: 1px solid #e6ecf0;
  border-radius: 10px;
}

.todo-input:focus {
  border: 1px solid #a427a4;
}

.button-add {
  width: 90px;
  padding: 15px 15px;
  font-size: 20px;
  border-radius: 10px;
  border: 0;
  background-color: #a427a4;
  color: #e6ecf0;
  cursor: pointer;
}

.button-add:hover {
  background-color: #a83ea8;
  box-shadow: 3px 3px 27px #a427a4;
}

.todo-item {
  display: flex;
  margin: 20px 0;
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 10px;
  max-height: 30px;
}

.button-done,
.button-edit,
.button-delete {
  border: none;
  outline: none;
  cursor: pointer;
  background-color: transparent;
  font-size: 28px;
  border-radius: 50%;
}

.button-done {
  color: #ae59ae;
  margin-right: 10px;
}

.button-done:hover {
  color: #e182e1;
}

.button-edit {
  color: #947f94;
  margin-right: 10px;
}

.button-edit:hover {
  color: #cfc7cf;
}

.button-delete {
  color: #660748;
}

.button-delete:hover {
  color: #a80c76;
}

.done {
  text-decoration-style: solid;
  text-decoration-line: line-through;
  text-decoration-color: #e904c3;
  opacity: 0.6;
}

.todo-list {
  width: 240px;
  background-color: inherit;
  border: none;
  color: #ccc;
  font-size: 20px;
  padding-left: 10px;
  margin-right: 15px;
}

input:focus,
button:focus {
  outline: none;
}

@media only screen and (max-width: 481px) {
  .app-container {
    background: linear-gradient(to right bottom, #1d1d28, #212125);
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .todo-container {
    background-color: #161617;
    width: 350px;
    min-width: 300px;
    height: 600px;
    min-height: 600px;
    padding: 20px;
    box-sizing: border-box;
    border-radius: 10px;
    box-shadow: 3px 6px 30px #a427a4;
    margin-bottom: 10px;
    overflow-y: auto;
  }

  .header h1 {
    color: #e6ecf0;
    text-align: center;
    margin: 20px 0;
  }

  .todo-input {
    outline: none;
    width: 190px;
    padding: 10px;
    margin-right: 13px;
    font-size: 20px;
    color: #ccc;
    background-color: #333232;
    border: 1px solid #e6ecf0;
    border-radius: 10px;
  }

  .button-add {
    width: 70px;
    padding: 13px 0px;
    font-size: 19px;
    border-radius: 10px;
    border: 0;
    background-color: #a427a4;
    color: #e6ecf0;
    cursor: pointer;
  }

  .todo-item {
    display: flex;
    margin: 10px 0px;
    border: 1px solid #ccc;
    border-radius: 10px;
    padding: 8px;
    max-height: 30px;
  }

  .button-done,
  .button-edit,
  .button-delete {
    border: none;
    outline: none;
    cursor: pointer;
    background-color: transparent;
    font-size: 25px;
    border-radius: 50%;
  }

  .button-done {
    color: #ae59ae;
    margin-right: 5px;
  }

  .button-edit {
    color: #947f94;
    margin-right: 5px;
  }

  .todo-list {
    width: 190px;
    background-color: inherit;
    border: none;
    color: #ccc;
    font-size: 18px;
    padding-left: 8px;
    margin-right: 1px;
  }
}
